<template lang="pug">
  .header-wrap
    .company-ingo
      .logo
      span.name 北京*****公司
    slot
    .logout(v-if="showLogOut" @click="logOut") 退出登录
    .user-Name {{userName}}
    .current-data {{currentTime}}
</template>

<script>
  import Cookies from 'js-cookie';
  import {syeVersion} from 'constant';
  import {getYMD} from 'util';
  import api from 'api';

  export default {
    name: 'HelloWorld',
    data () {
      return {
        userName: '',
        currentTime: '',
        syeVersion: syeVersion,
      }
    },
    computed:{
      showLogOut(){
        return Cookies.get('token');
      },
    },
    mounted(){
      setInterval(this.setTime, 1000);
      this.getUserInfo();
    },
    methods:{
      getUserInfo(){
        api.core.getUserInfo()
          .then(({data}) => {
            console.log(data);
            this.userName = data.data.userName;
            console.log(this.userName);
          });
      },
      setTime(){
        let vWeek,vWeek_s,vDay;
        vWeek = ["星期天","星期一","星期二","星期三","星期四","星期五","星期六"];
        let date =  new Date();
        let year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hours = date.getHours(),
        minutes = date.getMinutes(),
        seconds = date.getSeconds();
        vWeek_s = date.getDay();
        this.currentTime = year + "年" + month + "月" + day + "日" + "\t" + hours + ":" + minutes +":" + seconds ;
      },
      logOut(){
        Cookies.remove('token');
        this.$router.push({
          name: 'Login',
        });
      },
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .header-wrap{
    padding: 0 20px;
    width: 100%;
    height:  $header-height;
    background: $base-color;
    .company-ingo {
      height: $header-height;
      float: left;
      color: #fff;
      .logo{
        background: url("~assets/logo.png");
        width: 80px;
        height: $header-height;
        background-size: 80px $header-height;
        background-repeat: no-repeat;
        display: inline-block;
        float: left;
      }
      .name,.img,.version{
        float: left;
        display: inline-block;
        margin-left: 5px;
      }
      .name{
        font-size: 14px;
        color: #ccc;
        height: $header-height;
        line-height: $header-height;
      }
      .version{
        color: #ccc;
        line-height: $header-height;
      }
    }
    .current-data,.logout,.user-Name{
      color: #fff;
      float: right;
      height: 100%;
      line-height: 60px;
    }
    .logout,.user-Name{
      cursor: pointer;
      margin-left: 10px;
    }
  }
</style>



